<template>
  <div ref="slider" class="slider">
    <div ref="sliderContent" class="slider-content move-transition">
      <!-- <div class="img-content">
          <img :src="imgFirst" :width="width" :height="height">
      </div> -->
      <div class="img-content" v-for="(item, index) in imgs" :key="index" @click="gotoLink(item.src)">
          <img :src="item.img" :width="width" :height="height">
      </div>
      <div class="img-content">
          <img :src="imgLast" :width="width" :height="height">
      </div>
    </div>
    <ul class="btn-content">
      <li @mouseover="btnChangeImg(index)" @mouseout="btnMoveStart" class="btn" v-for="(item, index) in imgs" :key="index">
        <span :class="{active: imgNow === index}">{{ index + 1 }}</span>
      </li> 
    </ul>
  </div>
</template>

<script>
  export default {
    props: {
      width: {
        type: Number
      },
      height: {
        type: Number  
      },
      imgs: {
        type: Array
      }
    },
    data () {
      return {
        imgNow: 0
      }
    },
    created() {
      // 在末尾添加一张图片（存放第一张图）
      this.imgLast = this.imgs[0].img

      // 初始化 slider-content 需要 tanslate 的距离，moveCount 用来计算位移距离
      // haveTransition 保存元素是否使用 transition
      this.moveWidth = 0, this.moveCount = 0, this.haveTransition = true
    },
    mounted () {
      // 通过传入的高宽，来定义轮播图的高宽
      let slider = this.$refs.slider
      slider.style.width = this.width + 'px'
      slider.style.height = this.height + 'px'

      // 计算所有图片的宽度（算上后加的），然后设置 slider-content 的宽度，
      // 然后通过 translate 来轮播图片
      let sliderContent = this.$refs.sliderContent
      let allImgsWidth = this.width * (this.imgs.length + 1)
      sliderContent.style.width = allImgsWidth + 'px'

      // setInterval 需要的函数单独拿出来
      let imgMove = () => {
        // 如果 move-transition 不存在，那么就加上，存在则跳过
        if (!this.haveTransition) {
          sliderContent.className = 'slider-content move-transition'
          this.haveTransition = true
        }
        this.moveCount++
        if (this.moveCount === this.imgs.length) {
          this.imgNow = 0
        } else {
          this.imgNow = this.moveCount
        }
        this.moveWidth = this.width * this.moveCount
        sliderContent.style.transform = `translate3d(-${this.moveWidth}px, 0, 0)`
        if (this.moveCount === this.imgs.length) {
          this.changeFirst = setTimeout(() => {
            sliderContent.className = 'slider-content'
            this.moveWidth = 0
            this.moveCount = 0
            sliderContent.style.transform = `translate3d(-${this.moveWidth}px, 0, 0)`
            this.haveTransition = false
          }, 500)
        }
      }

      this.move = setInterval(imgMove, 1500)

      this.start = function () {
        this.move = setInterval(imgMove, 1500)
      }
    },
    methods: {
      // 当鼠标放在图片选择按钮上时，停止循环，并显示选择的图片
      btnChangeImg (index) {
        let sliderContent = this.$refs.sliderContent
        if (!this.haveTransition) {
          sliderContent.className = 'slider-content move-transition'
          this.haveTransition = true
        }

        // 清除无限轮播循环，
        // 因为清除 setInterval 之后可能里面的 setTimeout 还在队列中然后出现 bug
        // 所以同时清楚 setInterval
        clearInterval(this.move)
        clearTimeout(this.changeFirst)

        // 将 imgNow，moveCount 设置为需要显示的图片的索引，然后计算需要移动的 translate
        this.imgNow = index
        this.moveCount = index
        this.moveWidth = this.moveCount * this.width
        sliderContent.style.transform = `translate3d(-${this.moveWidth}px, 0, 0)`
      },

      // 当鼠标移出图片选择按钮的时候，重新开始循环
      btnMoveStart () {
        this.start()
      },
      gotoLink (src) {
        window.open(src)
      }
    }
  }
</script>

<style lang="scss" scoped>
.slider {
  position: relative;
  overflow: hidden;
  .slider-content {
    float: left;
    font-size: 0;
  }
  .img-content {
    // letter-spacing: -3px;
    float: left;
    cursor: pointer;
  }
  .move-transition {
    transition: all 0.5s;
  }
  .btn-content {
    position: absolute;
    bottom: 10px;
    right: 10px;
    color: rgb(242, 151, 95);
    font-size: 12px;
   .btn {
      display: inline-block;
      text-align: center;
      width: 20px;
      height: 20px;
      margin: 0 3px;
     span {
      display: inline-block;
      width: 16px;
      height: 16px;
      line-height: 16px;
      text-align: center;
      background: white; 
      border: 1.1px solid rgb(204, 187, 171);
      transition: all 0.2s;
      &:hover,
      &.active {
        width: 18px;
        height: 18px;
        line-height: 18px;
        cursor: default;
        color: white;
        background: rgb(204, 187, 171);
      }
     }
    }
  }
}
</style>
